.global-nav {
  position: fixed;
  right: -320px;
  /* これで隠れる */
  top: 0;
  width: 300px;
  /* スマホに収まるくらい */
  height: 100vh;
  padding-top: 40px;
  background-color: #c9c3c3;
  transition: all .6s;
  z-index: 200;
  overflow-y: auto;
  /* メニューが多くなったらスクロールできるように */ }

.hamburger {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  /* クリックしやすいようにちゃんと幅を指定する */
  height: 40px;
  /* クリックしやすいようにちゃんと高さを指定する */
  cursor: pointer;
  z-index: 300;
  display: none; }

.global-nav__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.global-nav__item {
  text-align: center;
  padding: 0 14px; }

.global-nav__item a {
  display: block;
  padding: 8px 0;
  border-bottom: 1px solid #eee;
  text-decoration: none;
  color: #111; }

.global-nav__item a:hover {
  background-color: #a39d9d; }

.hamburger__line {
  position: absolute;
  left: 11px;
  width: 18px;
  height: 1px;
  background-color: #111;
  transition: all .6s; }

.hamburger__line--1 {
  top: 14px; }

.hamburger__line--2 {
  top: 20px; }

.hamburger__line--3 {
  top: 26px; }

.black-bg {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  background-color: #000;
  opacity: 0;
  visibility: hidden;
  transition: all .6s;
  cursor: pointer; }

/* 表示された時用のCSS */
.nav-open .global-nav {
  right: 0; }

.nav-open .black-bg {
  opacity: .8;
  visibility: visible; }

.nav-open .hamburger__line--1 {
  transform: rotate(45deg);
  top: 20px; }

.nav-open .hamburger__line--2 {
  width: 0;
  left: 50%; }

.nav-open .hamburger__line--3 {
  transform: rotate(-45deg);
  top: 20px; }

@media screen and (max-width: 800px) {
