@media screen and (max-width: 2000px) {


    section {
        width: 100%;
    }

    .section-pic {
        width: 100%;
        height: 850px;
    }

    .secpic {
        object-fit: cover;
    }

    .hide {
        object-fit: cover;
        opacity: 0;
    }

    .hide:hover {
        object-fit: cover;
        opacity: 1;
    }

